import React, { useState } from 'react';
import { connect } from 'react-redux';
import { Tab, Grid, Button, Modal } from 'semantic-ui-react';
import { withRouter } from 'react-router-dom';
import RecipeCard from '../components/RecipeCard';
import { removeUser } from '../reducers/userReducer';
import { notify } from '../reducers/notificationReducer';

const User = ({ loggedUser, user, selectedTab, removeUser, history, notify }) => {
  const [showRemoveModal, setShowRemoveModal] = useState(false);
  if (!user) return null;

  const loggedUserProfile = loggedUser && loggedUser.id === user.id;

  const recipeGrid = (recipes) => {
    return (
      <Grid padded stackable columns={4}>
        {recipes.map(recipe => <RecipeCard key={recipe.id} recipe={recipe} />)}
      </Grid>
    );
  };

  const paneStyle = {
    minHeight: '200px',
    marginBottom: '20px'
  };

  const recipePane = <Tab.Pane style={paneStyle}>
    {user.recipes.length === 0 ? 'Ei listtyj reseptej' : recipeGrid(user.recipes)}
  </Tab.Pane>;

  const favoritesPane = <Tab.Pane style={paneStyle}>
    {user.favoriteRecipes.length === 0 ? 'Ei suosikkireseptej' : recipeGrid(user.favoriteRecipes)}
  </Tab.Pane>;

  const panesToShow = loggedUserProfile
    ? [{ menuItem: { key: 'all', icon: 'book', content: 'Omat reseptit' }, render: () => recipePane },
      { menuItem: { key: 'favorites', icon: 'star', content: 'Suosikit' }, render: () => favoritesPane }]
    : [{ menuItem: { key: 'all', icon: 'book', content: 'Kyttjn reseptit' }, render: () => recipePane }];

  const handleRemove = async () => {
    setShowRemoveModal(false);
    try {
      await removeUser(user.id);
      history.push('/');
      notify('Kyttjtili poistettu!', 'success');
    } catch (error) {
      notify('Kyttjtilin poistaminen ei onnistunut', 'error');
    }
  };

  const removeUserModal = () => (
    <Modal size="tiny" open={showRemoveModal} onClose={() => setShowRemoveModal(false)}>
      <Modal.Header>Poistetaanko kyttjtili?</Modal.Header>
      <Modal.Content>
        <p>Haluatko varmasti poistaa kyttjtilisi?
          Jos poistat kyttjn, et voi en kirjautua sisn, luoda reseptej, muokata aiemmin luomiasi reseptej jne.
        </p>
        <p>
          Kyttjn poistaminen on peruuttamatonta, eik poistettua kyttj voida en palauttaa.
        </p>
      </Modal.Content>
      <Modal.Actions>
        <Button onClick={() => setShowRemoveModal(false)}>Peruuta</Button>
        <Button
          color="red"
          icon="user delete"
          labelPosition="left"
          content="Poista kyttj"
          onClick={handleRemove} />
      </Modal.Actions>
    </Modal>
  );

  return (
    <>
      {removeUserModal()}
      <h2>{user.username}</h2>
      <Button
        icon="user delete"
        color="red"
        labelPosition="left"
        content="Poista kyttjtili"
        style={{ marginBottom: '-100px', float: 'right' }} 
        onClick={() => setShowRemoveModal(true)}/>
      <Tab panes={panesToShow} defaultActiveIndex={selectedTab ? Number(selectedTab.tab) || 0 : 0} />
    </>
  );
};

export default connect(
  state => ({
    loggedUser: state.loggedUser
  }),
  { removeUser, notify }
)(withRouter(User));